import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'

export default {
	components: {
		uniCalendar
	},
	data() {
		/**
		 * 时间计算
		 */
		function getDate(date, AddDayCount = 0) {
			if (typeof date !== 'object') {
				date = date.replace(/-/g, '/')
			}
			let dd = new Date(date)
			dd.setMonth(dd.getMonth() + AddDayCount) // 获取AddDayCount天后的日期
			let y = dd.getFullYear()
			let m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1 // 获取当前月份的日期，不足10补0
			let d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate() // 获取当前几号，不足10补0
			return y + '-' + m + '-' + d
		}
		let tags = [{
				id: 0,
				name: '农历',
				checked: false,
				attr: 'lunar'
			},
			{
				id: 1,
				name: '固定高度',
				checked: false,
				attr: 'fixedHeihgt'
			},
			{
				id: 2,
				name: '垂直滚动',
				checked: false,
				attr: 'vertical'
			},
			{
				id: 3,
				name: '水平滚动',
				checked: false,
				attr: 'horizontal'
			},
			{
				id: 4,
				name: '开始日期(' + getDate(new Date(), -1) + ')',
				checked: false,
				value: getDate(new Date(), -1),
				attr: 'startDate'
			},
			{
				id: 5,
				name: '结束日期(' + getDate(new Date(), 2) + ')',
				value: getDate(new Date(), 2),
				checked: false,
				attr: 'endDate'
			},
			{
				id: 6,
				name: '禁用今天之前的日期',
				checked: false,
				attr: 'disableBefore'
			},
			{
				id: 7,
				name: '自定义当前日期(' + getDate(new Date(), 1) + ')',
				value: getDate(new Date(), 1),
				checked: false,
				attr: 'date'
			}
		]

		return {
			show: false,
			tags,
			slide: 'none',
			date: '',
			startDate: '',
			endDate: '',
			timeData: {}
		}
	},
	onLoad() {},
	methods: {
		closeMask() {
			this.show = false
		},
		toggle(index, item) {
			this.tags[index].checked = !item.checked
			// item.checked = !item.checked;
			console.log(index)
			if (index === 2) {
				this.tags[3].checked = false
			}
			if (index === 3) {
				this.tags[2].checked = false
			}
			// this.attribute[item.attr] = !item.checked;
		},
		open() {
			if (this.tags[3].checked) {
				this.slide = 'horizontal'
			} else if (this.tags[2].checked) {
				this.slide = 'vertical'
			} else {
				this.slide = 'none'
			}
			if (this.tags[4].checked) {
				this.startDate = this.tags[4].value
			} else {
				this.startDate = ''
			}
			if (this.tags[5].checked) {
				this.endDate = this.tags[5].value
			} else {
				this.endDate = ''
			}
			if (this.tags[7].checked) {
				this.date = this.tags[7].value
			} else {
				this.date = ''
			}
			this.show = true
			console.log(this.date)
		},
		change(e) {
			console.log('change 返回:', e.fulldate)
			this.timeData = e
		},
		toClick(e) {
			console.log('点击事件', e.fulldate)
			this.timeData = e
		},
		confirm() {
			this.show = false
		}
	}
}
